import React from 'react';
import {CellProps, Column} from 'react-table';
import './App.css';
import {Archive, ArchiveContainer, fetchDataParams} from "./types";
import ArchiveTable from "./components/ArchiveTable";
import qs from "qs";
import moment from 'moment';
import {fileSizeIEC} from "./core/utils";


function App() {

  const columns: Array<Column<Archive>> = React.useMemo(
    () => [
      {
        Header: 'Archive',
        columns: [
          {
            Header: 'Id',
            accessor: 'id',
            Cell: ({ row }: CellProps<Archive>) => (
              <div>
                {<a href={row.original.url}>{row.original.id}</a>}
              </div>
            ),
          },
          {
            Header: 'Title',
            accessor: 'title',
          },
          {
            Header: 'Count',
            accessor: 'filecount',
          },
          {
            Header: 'Size',
            accessor: 'filesize',
            Cell: ({ value }) => {
              return fileSizeIEC(value);
            },
          },
          {
            Header: 'Public Date',
            accessor: 'public_date',
            Cell: ({ value }) => {
              return value ? moment.unix(value).format('MM-DD-YYYY'): 'Empty';
            },
          },
          {
            Header: 'Create Date',
            accessor: 'create_date',
            Cell: ({ value }) => {
              return value ? moment.unix(value).format('MM-DD-YYYY'): 'Empty';
            },
          },
          {
            Header: 'Last Modified',
            accessor: 'last_modified',
            Cell: ({ value }) => {
              return value ? moment.unix(value).format('MM-DD-YYYY'): 'Empty';
            },
          },
          {
            Header: 'Source',
            accessor: 'source_type',
          },
          {
            Header: 'Reason',
            accessor: 'reason',
          },
          // {
          //   Header: 'Download',
          //   accessor: 'download',
          //   disableSortBy: true
          // },
          // {
          //   Header: 'Thumbnail',
          //   accessor: 'thumbnail',
          //   disableSortBy: true
          // },
        ],
      },
      // {
      //   Header: 'Tags',
      //   accessor: 'tags',
      // },
      // {
      //   Header: 'Manage Entries',
      //   accessor: 'manage_entries',
      // },
      {
        Header: 'Gallery',
        columns: [
          {
            Header: 'Id',
            accessor: 'gallery.id',
            Cell: ({ row }: CellProps<Archive>) => (
              <div>
                {row.original.gallery ? <a href={row.original.gallery.url}>{row.original.gallery.id}</a> : ''}
              </div>
            ),
          },
          {
            Header: 'Link',
            accessor: 'gallery.link',
            Cell: ({ value }: CellProps<Archive>) => {
              return <a href={value}>{value}</a>;
            },
            disableSortBy: true
          },
        ]
      },
    ],
    []
  )

  const baseURL = '/manage-archives/?'

  // We'll start our table without any data
  const [data, setData] = React.useState<Archive[]>([])
  const [loading, setLoading] = React.useState(false)
  const [params, setParams] = React.useState('json=1')
  const [pageCount, setPageCount] = React.useState(0)
  const [resultTotal, setResultTotal] = React.useState(0)
  const fetchIdRef = React.useRef(0)

  const skipPageResetRef = React.useRef(false);

  const fetchData = React.useCallback(({ pageSize, pageIndex, sortBy }: fetchDataParams) => {
    // This will get called when the table needs new data
    // You could fetch your data from literally anywhere,
    // even a server. But for this example, we'll just fake it.
    skipPageResetRef.current = true;
    const currentSearch = qs.parse(params, { ignoreQueryPrefix: true });
    currentSearch.page = String(pageIndex + 1);
    currentSearch.size = String(pageSize);
    if (sortBy.length > 0)
      currentSearch.sort_by = JSON.stringify(sortBy);

    const newParams = qs.stringify(currentSearch, { indices: false });
    const newUrl = `${baseURL}${newParams}`

    setParams(newParams)

    // Give this fetch an ID
    const fetchId = ++fetchIdRef.current

    // Set the loading state
    setLoading(true)

    fetch(newUrl)
      .then(response => response.json())
      .then((data: ArchiveContainer) => {
        if (fetchId === fetchIdRef.current) {
          console.log(data.results);
          setData(data.results);
          setPageCount(data.num_pages);
          setResultTotal(data.count);
          setLoading(false);
        }
      });
  }, [params])

  React.useEffect(() => {
    skipPageResetRef.current = false;
  }, [data]);

  return (
    <main className="App">
      <div>
        <ArchiveTable
          columns={columns}
          data={data}
          fetchData={fetchData}
          loading={loading}
          pageCount={pageCount}
          totalResults={resultTotal}
          skipPageReset={skipPageResetRef.current}
        />
      </div>
    </main>
  )
}

export default App;