import React from 'react'
import { Table as BTable } from 'reactstrap';
import {useTable, Column, usePagination, useSortBy, Row, useAsyncDebounce} from 'react-table';
import {Archive, fetchDataParams} from "../types";
import IndeterminateCheckbox from "./IndeterminateCheckbox";


interface Table<T extends object> {
  columns: Array<Column<T>>;
  data: T[];
  fetchData: ({ pageSize, pageIndex, sortBy }: fetchDataParams) => void;
  skipPageReset?: boolean | undefined;
  pageCount?: number;
  totalResults?: number;
  loading: boolean;
}

export default function ArchiveTable({
                                       columns,
                                       data,
                                       fetchData,
                                       loading,
                                       pageCount: controlledPageCount,
                                       totalResults,
                                       skipPageReset = false,
}: Table<Archive>) {
  // Use the state and functions returned from useTable to build your UI
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    allColumns,
    page,
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    getToggleHideAllColumnsProps,
    // Get the state from the instance
    state: { pageIndex, pageSize, sortBy },
  } = useTable<Archive>(
    {
      columns,
      data,
      initialState: { pageIndex: 0 }, // Pass our hoisted table state
      manualPagination: true, // Tell the usePagination
      // hook that we'll handle our own data fetching
      // This means we'll also have to provide our own
      // pageCount.
      manualSortBy: true,
      pageCount: controlledPageCount,
      autoResetPage: !skipPageReset,
      autoResetSortBy: !skipPageReset,
      // autoResetFilters: !skipPageReset,
    },
    useSortBy,
    usePagination,
  )

  const onFetchDataDebounced = useAsyncDebounce(fetchData, 50);

  React.useEffect(() => {
    onFetchDataDebounced({ pageIndex, pageSize, sortBy })
  }, [onFetchDataDebounced, pageIndex, pageSize, sortBy])

  const Pagination = <div className="pagination">
    <button onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
      {'<<'}
    </button>{' '}
    <button onClick={() => previousPage()} disabled={!canPreviousPage}>
      {'<'}
    </button>{' '}
    <button onClick={() => nextPage()} disabled={!canNextPage}>
      {'>'}
    </button>{' '}
    <button onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
      {'>>'}
    </button>{' '}
    <span>
          Page{' '}
      <strong>
            {pageIndex + 1} of {pageOptions.length}
          </strong>{' '}
        </span>
    <span>
          | Go to page:{' '}
      <input
        type="number"
        defaultValue={pageIndex + 1}
        onChange={e => {
          const page = e.target.value ? Number(e.target.value) - 1 : 0
          gotoPage(page)
        }}
        style={{ width: '100px' }}
      />
        </span>{' '}
    <select
      value={pageSize}
      onChange={e => {
        setPageSize(Number(e.target.value))
      }}
    >
      {[10, 20, 50, 100].map(pageSize => (
        <option key={pageSize} value={pageSize}>
          Show {pageSize}
        </option>
      ))}
    </select>
  </div>

  // Render the UI for your table
  return (
    <>
      <div>
        <div>
          <IndeterminateCheckbox name={'all'} {...getToggleHideAllColumnsProps()} /> Toggle
          All
        </div>
        {allColumns.map(column => (
          <div key={column.id}>
            <label>
              <input type="checkbox" {...column.getToggleHiddenProps()} />{' '}
              {column.id}
            </label>
          </div>
        ))}
        <br />
      </div>
      <div>
        {Pagination}
        <div>
          {loading ? (
            // Use our custom loading state to show a loading indicator
            <div>Loading...</div>
          ) : (
            <div>
              Showing {page.length} of {totalResults || 0}{' '}
              results
            </div>
          )}
        </div>
      </div>
    <BTable bordered size="sm" className={"generic-table"} {...getTableProps()}>
      <thead>
      {headerGroups.map(headerGroup => (
        <tr {...headerGroup.getHeaderGroupProps()}>
          {headerGroup.headers.map(column => (
            <th {...column.getHeaderProps()}>
              <div>
                <span {...column.getSortByToggleProps()}>
                                            {column.render('Header')}
                  {/* Add a sort direction indicator */}
                  {column.isSorted ? (column.isSortedDesc ? ' ðŸ”½' : ' ðŸ”¼') : ''}
                                        </span>
              </div>
              {/* Render the columns filter UI */}
              <div>{column.canFilter ? column.render('Filter') : null}</div>
            </th>
          ))}
        </tr>
      ))}
      </thead>
      <tbody {...getTableBodyProps()}>
      {page.map((row: Row<Archive>) => {
        prepareRow(row)
        return (
          <tr {...row.getRowProps()}>
            {row.cells.map(cell => {
              return <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
            })}
          </tr>
        )
      })}
      <tr>
        {loading ? (
          // Use our custom loading state to show a loading indicator
          <td colSpan={10000}>Loading...</td>
        ) : (
          <td colSpan={10000}>
            Showing {page.length} of {totalResults || 0}{' '}
            results
          </td>
        )}
      </tr>
      </tbody>
    </BTable>
      {Pagination}
  </>
  )
}